r);\r\n    font-family: \"Noto Sans\";\r\n}\r\n\r\nbody {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n/*---HEADER---*/\r\nheader {\r\n    flex: 0 0 60px;\r\n    background-color: var(--primary-color);\r\n    color: var(--header-color);\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: flex-start;\r\n    padding: 1rem;\r\n    gap: 1rem;\r\n}\r\n\r\nheader h1 {\r\n    font-size: 1.8rem;\r\n}\r\n\r\n.logo {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 0.5rem;\r\n}\r\n.logo > img {\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    object-fit: cover;\r\n}\r\n.logo span {\r\n    font-size: 1.8rem;\r\n    font-weight: 50;\r\n    margin-left: 0.5rem;\r\n}\r\n\r\nheader .menu {\r\n    width: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: flex-start;\r\n}\r\n\r\n/* Toggle */\r\n\r\n.toggle-switch {\r\n    position: relative;\r\n    width: 5rem;\r\n    height: 2rem;\r\n    margin: 1rem;\r\n    --light: #d8dbe0;\r\n    --dark: #28292c;\r\n    --link: rgb(27, 129, 112);\r\n    --link-hover: rgb(24, 94, 82);\r\n}\r\n\r\n.switch-label {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 2rem;\r\n    background-color: var(--dark);\r\n    border-radius: 1rem;\r\n    cursor: pointer;\r\n    border: 3px solid var(--dark);\r\n}\r\n\r\n.checkbox {\r\n    position: absolute;\r\n    display: none;\r\n}\r\n\r\n.slider {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    border-radius: 2rem;\r\n    -webkit-transition: 0.3s;\r\n    transition: 0.3s;\r\n}\r\n\r\n.checkbox:checked ~ .slider {\r\n    background-color: var(--light);\r\n}\r\n\r\n.slider::before {\r\n    content: \"\";\r\n    position: absolute;\r\n    top: 0.25rem;\r\n    left: 1px;\r\n    width: 1rem;\r\n    height: 1rem;\r\n    border-radius: 50%;\r\n    -webkit-box-shadow: inset 0.4rem -0.1rem 0px 0px var(--light);\r\n    box-shadow: inset 0.4rem -0.1rem 0px 0px var(--light);\r\n    background-color: var(--dark);\r\n    -webkit-transition: 0.3s;\r\n    transition: 0.3s;\r\n}\r\n\r\n.checkbox:checked ~ .slider::before {\r\n    -webkit-transform: translateX(3rem);\r\n    -ms-transform: translateX(3rem);\r\n    transform: translateX(3rem);\r\n    background-color: var(--dark);\r\n    -webkit-box-shadow: none;\r\n    box-shadow: none;\r\n}\r\n\r\n/* */\r\n\r\nbutton {\r\n    width: 100%;\r\n    padding: 10px 20px;\r\n    border: none;\r\n    border-radius: 0.2rem;\r\n    background-color: var(--secondary-color);\r\n    color: var(--text-color);\r\n    font-size: 1.1rem;\r\n    font-weight: bold;\r\n    cursor: pointer;\r\n}\r\n\r\nheader > button {\r\n    max-width: 300px;\r\n}\r\n\r\n#formContainer {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100vw;\r\n    height: 100vh;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background-color: rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n#formContainer .form {\r\n    width: 100vw;\r\n    max-width: 500px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 1rem;\r\n    background-color: var(--primary-color);\r\n    padding: 2rem;\r\n    position: relative;\r\n}\r\n\r\n.form > p,\r\n.form a {\r\n    color: var(--header-color);\r\n    font-size: 1.1rem;\r\n    text-align: justify;\r\n}\r\n\r\n.form > img {\r\n    width: 100%;\r\n    height: 7rem;\r\n    object-fit: contain;\r\n    margin-bottom: 1rem;\r\n    mix-blend-mode: screen;\r\n}\r\n\r\n.legals {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    gap: 1rem;\r\n}\r\n\r\n.loader-container {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: rgba(30, 30, 30, 0.8);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    z-index: 99;\r\n}\r\n\r\n.loader {\r\n    transform: translate(-50%, -50